<template>
<!-- 充值界面充值金额部分 -->
    <div class="payment">
        <ul>
            <li @click="pay(item.coupons)" v-for="(item,index) in $store.state.user.payment" :key="index">{{item.coupons}}点劵
                <p>{{item.money}}</p>
            </li>
        </ul>
    </div>
</template>
<script>
import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);
export default {
    methods:{
        pay(coupons){
            Toast('支付成功');
            this.$store.commit('user/pay',coupons);
        }
    }
}
</script>
<style lang="scss" scoped>
    .payment{
      ul{
          margin:2rem 0;
          list-style: none;
          display:flex;
          flex-wrap: wrap;
          justify-content: center;
          li{
              border:1px solid green;
              font-size:0.5rem;
              border-radius: 0.4rem;
              padding:1rem 1rem;
              text-align: center;
              margin-bottom:0.75rem;
              p{
                  margin:0px;
              }
          }
          li:nth-child(2n){
              margin:0 0.75rem 0.75rem;
          }
      }
    }
    
    
</style>